.home {
   max-width: 1170px;
   margin: 0 auto;
   background-color: #f7f3f3;
   height: 100%;

   .home__up {
      position: relative;
      display: flex;
      justify-content: center;
      padding-right: 24px;
      padding-left: 24px;
      margin-top: 10px;


      @media (max-width: 768px) and (min-width: 570px) {
         padding-left: 15px;
         padding-right: 15px;
      }

      @media (max-width: 569px) {
         display: flex;
         position: relative;
         align-items: center;
         justify-content: center;
         position: relative;
      }

      .home__link {
         position: absolute;
         right: 0;
         color: #EB3C3C;
         font-size: 20px;
         width: 35px;
         height: 35px;
         border-radius: 50%;
         background-color: white;
         display: flex;
         justify-content: center;
         align-items: center;
      }

      .home__title {
         text-align: center;
         color: #EB3C3C;
         font-family: 'Source Code Pro', monospace;
         font-style: italic;
         font-size: 28px;

         @media (min-width: 769px) {
            font-size: 40px;
            margin-bottom: 30px;
         }

         @media (max-width: 768px) and (min-width: 570px) {
            display: inline;
            font-size: 36px;
         }
      }
   }

   .home__btn {
      display: flex;
      justify-content: center;
   }

   .home__shoes__button {
      width: 150px;
      height: 40px;
      border: 2px solid #EB3C3C;
      border-radius: 10px;
      color: #EB3C3C;
      margin-top: 25px;
   }

   .input__box {
      display: flex;
      width: 100%;
      padding-right: 20px;
      border-radius: 15px;
      background-color: white;
      height: 40px;
      margin-bottom: 30px;

      @media (max-width: 768px) {
         margin-top: 30px;
         margin-bottom: 20px;
      }

      .input__icon-block {
         display: flex;
         align-items: center;
         font-size: 20px;
         color: #979797;
         height: 40px;
      }
   }

   .shoes-block {
      margin-top: 30px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      .shoes-block__item {
         width: auto;
         width: 24%;
         margin-bottom: 30px;
         cursor: pointer;

         @media (max-width: 1024px) and (min-width: 769px) {
            width: 32%;
            margin-bottom: 30px;
         }

         @media (max-width: 768px) and (min-width: 570px) {
            width: 49%;
         }

         @media (max-width: 569px) {
            width: 49%;
            margin-bottom: 20px;
         }
      }
   }

   .load__block{
      margin: 0 auto;
      width: 150px;
   }

   .load-more-button{
      text-align: center;
      margin-bottom: 15px;
      width: 100%;
      height: 40px;
      border-radius: 8px;
      background-color: #EB3C3C;
      color: white;
   }

   .load-more-button:hover{
      background-color: white;
      color: #EB3C3C;
      border: 2px solid #EB3C3C;
   }

}