.sorting-buttons{
   display: flex;
   gap: 20px;
   justify-content: center;
   flex-wrap: wrap;
   color: #8e8c8c;
   @media (min-width: 769px){
    font-size: 18px;
   }
   @media (max-width: 349px){
     gap: 5px;
     font-size: 16px;
   }
}

.sorting-buttons__item:hover{
  color: #EB3C3C;
  cursor: pointer;
}